.profile {
  box-shadow: 5px 15px 20px #706a6a;
  padding-top: 40px;
}

.description {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
}

.avatar {
  width: 100px;
  border-radius: 50%;
}

.name {
  font-size: 30px;
  font-weight: 700;
  margin-bottom: 20px;
}

.tag {
  font-size: 20px;
  color: rgb(133, 128, 128);
  text-align: center;
  margin-bottom: 10px;
}
.location {
  font-size: 20px;
  color: rgb(133, 128, 128);
  text-align: center;
}
.stats {
  display: flex;
  list-style: none;
  width: 100%;
  margin: 0;
  padding: 0;
}
.stats__item {
  border: 1px solid rgb(133, 128, 128);
  flex-basis: calc(100% / 3);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px;
}
.label {
  font-size: 16px;
  color: rgb(133, 128, 128);
  text-align: center;
}
.quantity {
  font-size: 20px;
  text-align: center;
  font-weight: 700;
}
